/* -------------------------------------------------------------------------- 
	Variables
  -------------------------------------------------------------------------- */
:root {
  --addonify_qv_modal_overlay_background: rgba(0, 0, 0, 0.8);
  --addonify_qv_modal_background: rgba(255, 255, 255, 1);
  --addonify_qv_modal_general_text_color: rgba(51, 51, 51, 1);
  --addonify_qv_modal_inputs_background_color: rgba(255, 255, 255, 1);
  --addonify_qv_modal_inputs_text_color: rgba(51, 51, 51, 1);
  --addonify_qv_modal_spinner_icon_color: rgba(51, 51, 51, 1);
  --addonify_qv_product_title: rgba(51, 51, 51, 1);
  --addonify_qv_product_excerpt: rgba(88, 88, 88, 1);
  --addonify_qv_product_rating_filled: rgba(245, 196, 14, 1);
  --addonify_qv_product_rating_empty: rgba(185, 185, 185, 1);
  --addonify_qv_product_price: rgba(51, 51, 51, 1);
  --addonify_qv_product_price_sale: rgba(255, 0, 0, 1);
  --addonify_qv_product_meta: rgba(2, 134, 231, 1);
  --addonify_qv_product_meta_hover: rgba(88, 88, 88, 1);
  --addonify_qv_close_button_text: rgba(118, 118, 118, 1);
  --addonify_qv_close_button_text_hover: rgba(2, 134, 231, 1);
  --addonify_qv_close_button_background: rgba(238, 238, 238, 1);
  --addonify_qv_close_button_background_hover: rgba(182, 222, 255, 1);
  --addonify_qv_misc_button_text: rgba(255, 255, 255, 1);
  --addonify_qv_misc_button_text_hover: rgba(255, 255, 255, 1);
  --addonify_qv_misc_button_background: rgba(51, 51, 51, 1);
  --addonify_qv_misc_button_background_hover: rgba(2, 134, 231, 1);
}

/* -------------------------------------------------------------------------- 
	Mixins
  -------------------------------------------------------------------------- */
/*
===========================================================

# Pugin: Addonify WooCommerce Quick View

# Author: https://addonify.com/

# Since: 28 July, 2020

=====================================
*/
/* -------------------------------------------------------------------------- 
	Quick view button
  -------------------------------------------------------------------------- */
.adfy-qv-button {
  display: inline-block;
  padding: 0;
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  border: none;
  background-color: transparent;
  transition: all 0.5s ease;
}

.adfy-qv-button:focus {
  border: none;
  outline: none;
  background-color: transparent;
}

.adfy-qv-button:hover {
  border: none;
  background-color: transparent;
}

#addonify-qvm-close-button {
  width: 30px;
  height: 30px;
  line-height: 30px;
  font-size: 22px;
  font-weight: normal;
  border-radius: 100%;
  transition: all 0.5s ease;
}

/**
*
* Hide quick view button if disabled in mobile view is 'enabled' on the setting page.
*
* @since: 1.2.4
*/
@media (max-width: 575px) {
  .addonify-quick-view-disabled-on-mobile .addonify-qvm-button,
  .addonify-quick-view-disabled-on-mobile.woocommerce ul.products li.product .button.addonify-qvm-button,
  .addonify-quick-view-disabled-on-mobile.woocommerce-page ul.products li.product .button.addonify-qvm-button {
    display: none;
  }
}
/* -------------------------------------------------------------------------- 
	Quick view modal
  -------------------------------------------------------------------------- */
#addonify-quick-view-modal {
  display: block;
  position: fixed;
  visibility: hidden;
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 100000000;
  width: 100%;
  max-width: 1000px;
  height: auto;
  min-height: 400px;
  max-height: 80vh;
  overflow-x: hidden;
  overflow-y: auto;
  border-radius: 10px;
  background-color: var(--addonify_qv_modal_background);
  box-shadow: 0 0 65px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease;
}

#addonify-quick-view-overlay {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 10000000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 0.5s ease;
  background-color: var(--addonify_qv_modal_overlay_background);
}

body.addonify-qvm-is-active {
  position: relative;
  overflow-y: hidden;
  max-height: 100vh;
}

.addonify-qvm-is-active #addonify-quick-view-modal,
.addonify-qvm-is-active #addonify-quick-view-overlay {
  visibility: visible;
  opacity: 1;
}

.adfy-quick-view-model-inner {
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 100%;
}

.adfy-qvm-head {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 10;
}

.addonify-qvm-is-active .pswp {
  z-index: 1000000001;
}

.addonify-qvm-is-active .woocommerce img.pswp__img, .woocommerce-page img.pswp__img {
  background-color: white;
}

/* -------------------------------------------------------------------------- 
	Spinner
  -------------------------------------------------------------------------- */
#addonify-quick-view-modal #adfy-qvm-spinner {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 100000000;
  opacity: 1;
  visibility: visible;
}

#addonify-quick-view-modal #adfy-qvm-spinner .icon {
  color: var(--addonify_qv_modal_spinner_icon_color);
  animation-name: AddonifyQuickViewSpin;
  animation-duration: 500ms;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

#addonify-quick-view-modal #adfy-qvm-spinner.hide {
  display: none;
  opacity: 0;
  visibility: hidden;
}

/* -------------------------------------------------------------------------- 
	Modal box close button
  -------------------------------------------------------------------------- */
#addonify-qvm-close-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  fill: var(--addonify_qv_close_button_text);
  color: var(--addonify_qv_close_button_text);
  height: 30px;
  width: 30px;
  background-color: var(--addonify_qv_close_button_background);
  border-radius: 100%;
  cursor: pointer;
}
#addonify-qvm-close-button svg {
  height: 14px;
  width: 14px;
}
#addonify-qvm-close-button:hover {
  fill: var(--addonify_qv_close_button_text_hover);
  color: var(--addonify_qv_close_button_text_hover);
  background-color: var(--addonify_qv_close_button_background_hover);
}

/* -------------------------------------------------------------------------- 
	Modal box inner
  -------------------------------------------------------------------------- */
#addonify-quick-view-modal .adfy-quick-view-modal-content {
  position: relative;
  padding: 40px 30px;
  color: var(--addonify_qv_modal_general_text_color);
}

#addonify-quick-view-modal .woocommerce .product .onsale {
  position: absolute;
  right: auto;
  left: 20px;
  top: 20px;
  z-index: 10;
  margin: 0;
}
#addonify-quick-view-modal .woocommerce .product .product_title {
  color: var(--addonify_qv_product_title);
}
#addonify-quick-view-modal .woocommerce .product .price {
  color: var(--addonify_qv_product_price);
}
#addonify-quick-view-modal .woocommerce .product .price del {
  color: var(--addonify_qv_product_price);
}
#addonify-quick-view-modal .woocommerce .product .price ins {
  color: var(--addonify_qv_product_price_sale);
}
#addonify-quick-view-modal .woocommerce .product .woocommerce-product-details__short-description {
  color: var(--addonify_qv_product_excerpt);
}
#addonify-quick-view-modal .woocommerce .product .star-rating {
  color: var(--addonify_qv_product_rating_filled);
}
#addonify-quick-view-modal .woocommerce .product .star-rating::before {
  color: var(--addonify_qv_product_rating_empty);
}
#addonify-quick-view-modal .woocommerce .product .woocommerce-product-rating .woocommerce-review-link {
  color: var(--addonify_qv_product_meta);
}
#addonify-quick-view-modal .woocommerce .product .woocommerce-product-rating .woocommerce-review-link:hover {
  color: var(--addonify_qv_product_meta_hover);
}
#addonify-quick-view-modal .woocommerce .product form label {
  color: var(--addonify_qv_modal_general_text_color);
}
#addonify-quick-view-modal .woocommerce .product form input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--addonify_qv_modal_inputs_text_color);
  opacity: 1; /* Firefox */
}
#addonify-quick-view-modal .woocommerce .product form input:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: var(--addonify_qv_modal_inputs_text_color);
}
#addonify-quick-view-modal .woocommerce .product form input::-ms-input-placeholder { /* Microsoft Edge */
  color: var(--addonify_qv_modal_inputs_text_color);
}
#addonify-quick-view-modal .woocommerce .product form input[type=text],
#addonify-quick-view-modal .woocommerce .product form input[type=number] {
  color: var(--addonify_qv_modal_inputs_text_color);
  background-color: var(--addonify_qv_modal_inputs_background_color);
}
#addonify-quick-view-modal .woocommerce .product form select {
  color: var(--addonify_qv_modal_inputs_text_color);
  background-color: var(--addonify_qv_modal_inputs_background_color);
}
#addonify-quick-view-modal .woocommerce .product .product_meta a {
  color: var(--addonify_qv_product_meta);
}
#addonify-quick-view-modal .woocommerce .product .product_meta a:hover {
  color: var(--addonify_qv_product_meta_hover);
}
#addonify-quick-view-modal .woocommerce .product button,
#addonify-quick-view-modal .woocommerce .product .button {
  fill: var(--addonify_qv_misc_button_text);
  color: var(--addonify_qv_misc_button_text);
  background-color: var(--addonify_qv_misc_button_background);
}
#addonify-quick-view-modal .woocommerce .product button:hover,
#addonify-quick-view-modal .woocommerce .product .button:hover {
  fill: var(--addonify_qv_misc_button_text_hover);
  color: var(--addonify_qv_misc_button_text_hover);
  background-color: var(--addonify_qv_misc_button_background_hover);
}

#addonify-quick-view-modal .woocommerce .product::before,
#addonify-quick-view-modal .woocommerce .product::after {
  content: "";
  display: none;
}

#addonify-quick-view-modal .woocommerce .product .summary,
#addonify-quick-view-modal .woocommerce .product .woocommerce-product-gallery {
  margin-bottom: 0;
}

#addonify-quick-view-modal .woocommerce .product {
  display: grid;
  align-items: stretch;
  justify-content: stretch;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
}

#addonify-quick-view-modal .woocommerce .product .images,
#addonify-quick-view-modal .woocommerce .product .summary,
.woocommerce-page #addonify-quick-view-modal .product .images,
.woocommerce-page #addonify-quick-view-modal .product .summary {
  float: none;
  width: 100%;
  margin: 0;
  padding: 0;
}

/* -------------------------------------------------------------------------- 
	Responsive
  -------------------------------------------------------------------------- */
@media (max-width: 1100px) {
  #addonify-quick-view-modal {
    max-width: 95vw;
  }
}
@media (max-width: 768px) {
  #addonify-quick-view-modal .woocommerce .product {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 20px;
    column-gap: 0;
  }
  #addonify-quick-view-modal .woocommerce .product .images,
  #addonify-quick-view-modal .woocommerce .product .summary {
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 575px) {
  #addonify-quick-view-modal {
    max-width: 90vw;
    height: 90vh;
    min-height: 90vh;
    max-height: 90vh;
  }
  .adfy-quick-view-modal-content {
    padding: 30px 20px;
  }
}
/* -------------------------------------------------------------------------- 
	Keyframe
  -------------------------------------------------------------------------- */
@-webkit-keyframes AddonifyQuickViewSpin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes AddonifyQuickViewSpin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}